import HeroSectionPages from "./HeroSectionPages";
import HeaderSection from "./HeaderSections";
import PropertyValueCards from "./PropertyValueCards";
import HeroSectionCards from "./HeroSectionCards";
import img1 from "../images/value-icon-1.png";
import img2 from "../images/value-icon-2.png";
import img3 from "../images/value-icon-3.png";
import img4 from "../images/value-icon-4.png";
import imgP1 from "../images/Property-icon-1.png";
import imgP2 from "../images/Property-icon-2.png";
import imgP3 from "../images/Property-icon-3.png";
import imgP4 from "../images/Property-icon-4.png";

export default function ServicesPage() {
  return (
    <>
      <HeroSectionPages
        text={"Elevate Your Real Estate Experience"}
        title={
          "Welcome to Estatein, where your real estate aspirations meet expert guidance. Explore our comprehensive range of services, each designed to cater to your unique needs and dreams."
        }
      />
      <HeroSectionCards />
      <HeaderSection
        text={
          "Selling your property should be a rewarding experience, and at Estatein, we make sure it is. Our Property Selling Service is designed to maximize the value of your property, ensuring you get the best deal possible. Explore the categories below to see how we can help you at every step of your selling journey"
        }
        title={"Unlock Property Value"}
      />
      <PropertyValueCards
        titleOne={"Valuation Mastery"}
        textOne={
          "Discover the true worth of your property with our expert valuation services."
        }
        imgOne={img1}
        titleTwo={"Strategic Marketing"}
        textTwo={
          "Selling a property requires more than just a listing; it demands a strategic marketing."
        }
        titleThree={"Negotiation Wizardry"}
        textThree={
          "Negotiating the best deal is an art, and our negotiation experts are masters of it."
        }
        imgTwo={img2}
        imgThree={img3}
        titleFour={"Closing Success"}
        textFour={
          "A successful sale is not complete until the closing. We guide you through the intricate closing process."
        }
        imgFour={img4}
        titleFive={"Unlock the Value of Your Property Today"}
        textFive={
          "Ready to unlock the true value of your property? Explore our Property Selling Service categories and let us help you achieve the best deal possible for your valuable asset."
        }
      />

      <HeaderSection
        text={
          "Owning a property should be a pleasure, not a hassle. Estatein's Property Management Service takes the stress out of property ownership, offering comprehensive solutions tailored to your needs. Explore the categories below to see how we can make property management effortless for you"
        }
        title={"Effortless Property Management"}
      />
      <PropertyValueCards
        titleOne={"Tenant Harmony"}
        textOne={
          "Our Tenant Management services ensure that your tenants have a smooth and reducing vacancies."
        }
        imgOne={imgP1}
        titleTwo={"Maintenance Ease"}
        textTwo={
          "Say goodbye to property maintenance headaches. We handle all aspects of property upkeep."
        }
        titleThree={"Financial Peace of Mind"}
        textThree={
          "Managing property finances can be complex. Our financial experts take care of rent collection"
        }
        imgTwo={imgP2}
        imgThree={imgP3}
        titleFour={"Legal Guardian"}
        textFour={
          "Stay compliant with property laws and regulations effortlessly."
        }
        imgFour={imgP4}
        titleFive={"Experience Effortless Property Management"}
        textFive={
          "Ready to experience hassle-free property management? Explore our Property Management Service categories and let us handle the complexities while you enjoy the benefits of property ownership."
        }
      />
    </>
  );
}
